import config from './engagement-approval-view.config.json'
import JsonViewer from '@/components/JsonViewer'
import VueNumeric from 'vue-numeric'
export default {
    name: 'engagement-approval-view',
    data() {
        return {
            config,
            engagement: {
                id: '',
                clientId: '',
                clientName: '',
                address: '',
                npwp: '',
                pic: '',
                professionalFee: 0,
                caseOverview: '',
                description: '',
                notes: '',
                panitera: '',
                strategy: '',
                dmpName: '',
                dmpFeeSharing: 0,
                members: []
            },
            errorMessage: '',
            rEngagement: {}
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        getEngagement() {
            this.$http({
                method: 'get',
                url: `/engagement/manage-engagement/${this.engagement.id}`,
                headers: {
                    Authorization: 'Bearer ' + this.$store.getters.getToken
                }
            })
            .then(response => {
                let rEngagement = response.data[0]
                this.rEngagement = rEngagement
                this.engagement.clientId = rEngagement.client_id
                this.engagement.clientName = rEngagement.client_name
                this.engagement.address = rEngagement.address
                this.engagement.npwp = rEngagement.npwp
                this.engagement.pic = rEngagement.pic
                this.engagement.professionalFee = rEngagement.professional_fee
                this.engagement.caseOverview = rEngagement.case_over_view
                this.engagement.description = rEngagement.description
                this.engagement.notes = rEngagement.notes
                this.engagement.panitera = rEngagement.panitera
                this.engagement.strategy = rEngagement.strategy
                this.engagement.dmpName = rEngagement.dmp_name
                this.engagement.dmpFeeSharing = rEngagement.fee_share_dmp
                let rMembers = rEngagement.members
                for (let i = 0; i < rMembers.length; i++) {
                    let member = {
                        name: rMembers[i].member_name,
                        id: rMembers[i].employee_id,
                        feeSharing: rMembers[i].fee_share
                    }
                    this.engagement.members.push(member)
                }
            })
            .catch(error => {
                this.errorMessage = error.response.data.message
            })
        },
        handleApproval(_decision) {
            this.$bvModal.show('modal-loading')
            let rDecision = _decision == 'APPROVE' ? 'a' : 'r'
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: `/engagement/approval/${this.engagement.id}/by-admin`,
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        decision: rDecision
                    }
                })
                .then(response => {
                    this.$bvModal.hide('modal-loading')
                    this.$router.push('/admin/engagement/approval')
                })
                .catch(error => {
                    this.errorMessage = error.response.data.message
                    this.$bvModal.hide('modal-loading')
                })
            }, 1000)
        },
        handleApprove() {
            this.$bvModal.show('modal-approve')
        },
        handleReject() {
            this.$bvModal.show('modal-reject')
        },
        resetErrorMessage() {
            this.errorMessage = ''
        }
    },
    created() {
        this.engagement.id = this.$route.params.id
        this.getEngagement()
    }
}